import empty from '@/components/empty.vue';
export default {
	components: {
		empty
	},
	data() {
		return {
			orderList: []
		};
	},
	computed:{
		isEmpty(){
			return !this.orderList.length
		}
	},
	onShow() {
		this.initData()
	},
	methods: {
		initData() {
			const orderList = uni.getStorageSync('snackOrder') || []
			this.orderList = orderList
		},
		navUrl(item) {
			return `../cinema-detail/cinema-detail?cinemaId=${item.cinemaId}`
		},
		//删除订单
		deleteOrder(index) {
			let orderList = this.orderList.slice();
			orderList.splice(index, 1)
			uni.showModal({
				title: '提示',
				content: '确认删除订单吗？',
				success: (res) => {
					if (res.confirm) {
						this.orderList = orderList
						uni.setStorageSync('snackOrder', orderList)
					}
				}
			})
		}
	}
}
